//index.js
//获取应用实例
const app = getApp().globalData;
var util = require("../../../../utils/util.js");
var oid = "";
var oids = "";
var cids = "";

export default {
	data() {
		return {
			axis: [{
				id: '1',
				name: '带付款'
			}, {
				id: '2',
				name: '待配送'
			}, {
				id: '3',
				name: '配送中'
			}, {
				id: '4',
				name: '待评价'
			}, {
				id: '5',
				name: '已完成'
			}],
			ordersForGoods: [],
			or: "",
			oids: "",
			cids: "",
			oid: "",
			time: ""
		};
	},

	components: {},
	props: {},
	onLoad: function(e) {
		var that = this;
		getApp().globalData.initPage(that);
		console.log('订单id', e.oid);
		console.log('用户id', e.cid);
		let oid = JSON.parse(e.oid);
		let cid = e.cid;
		var id = getApp().globalData.userInfo.userId;
		that.setData({
			oids: oid,
			cids: cid
		});

		uni.request({
			url: getApp().globalData.url + '/order/orderSelectByIOpayStatus?oId=' + oid + '&' + 'o_customerInfo=' + id,
			data: {},
			header: {
				'content-type': 'application/json'
			},
			method: 'POST',
			dataType: 'json',
			success: function(res) {
				that.setData({
					ordersForGoods: res.data.data
				});
				console.log(res.data.data);
			}
		});

		uni.request({
			url: getApp().globalData.url + '/order/getSelectOrderoId?o_id=' + oid,
			data: {},
			header: {
				'content-type': 'application/json'
			},
			method: 'POST',
			dataType: 'json',
			success: function(res) {
				that.setData({
					oid: res.data
				});
				console.log("s" + res.data);
			}
		});

		var id = this.show();
		this.setData({
			oid: id
		}); // 调用函数时，传入new Date()参数，返回值是日期和时间

		var time = util.formatTime(new Date()); // 再通过setData更改Page()里面的data，动态更新页面的数据

		this.setData({
			time: time
		});
	},
	methods: {
		show: function() {
			return 1;
		},
		back: function() {
			uni.navigateBack({
				delta: 1
			});
		},
		tuiku: function(e) {
			var that = this;
			console.log(this.oids);
			var id = this.oids;
			uni.showModal({
				title: '确定要取消支付吗',
				success: function(res) {
					if (res.confirm) {
						//这里是点击了确定以后
						wx: wx.request({
							url: getApp().globalData.url + '/order/cancelTheorDer?oid=' + id,
							data: {},
							header: {
								'content-type': 'application/json'
							},
							method: 'POST',
							dataType: 'json',
							success: function(res) {
								wx.showToast({
									title: '取消订单成功',
									icon: 'success',
									duration: 500
								});
								that.tiaozhuan();
							}
						});
					}
					else {
						//这里是点击了取消以后
						console.log('用户点击取消');
					}
				}
			});
		},
		tiaozhuan: function() {
			uni.navigateTo({
				url: '/pages/payment/payment'
			});
		},
		refund: function(e) {
			console.log(e.currentTarget.dataset.oids);
			console.log(e.currentTarget.dataset.coids);
			let oid = e.currentTarget.dataset.oids;
			let oids = oid;
			let cid = e.currentTarget.dataset.coids;
			let cids = cid;
			uni.navigateTo({
				url: '/pages/addRefund/addRefund?oids=' + oids + '&cid=' + cids
			});
		},
		refund2: function(e) {
			console.log(e.currentTarget.dataset.oids);
			console.log(e.currentTarget.dataset.coids);
			let oid = e.currentTarget.dataset.oids;
			let oids = oid;
			uni.navigateTo({
				url: '/pages/comment/comment?oids=' + oids
			});
		},
		copyText: function(e) {
			console.log(e);
			uni.setClipboardData({
				data: e.currentTarget.dataset.text,
				success: function(res) {
					uni.getClipboardData({
						success: function(res) {
							uni.showToast({
								title: '复制成功'
							});
						}
					});
				}
			});
		},
		getId4: function(e) {
			console.log(e.currentTarget.dataset.oid);
			console.log('sdfgsdthtsrh-------' + e.currentTarget.dataset.cid);
			let oids = e.currentTarget.dataset.oid;
			let cids = e.currentTarget.dataset.cid;
			let oid = JSON.stringify(oids);
			let cido = JSON.stringify(cids);
			uni.navigateTo({
				url: '/pages/comment/comment?oid=' + oid + '&cid=' + cido
			});
		},
		setData: function(obj, callback) {
			let that = this;
			let keys = [];
			let val, data;
			Object.keys(obj).forEach(function(key) {
				keys = key.split('.');
				val = obj[key];
				data = that.$data;
				keys.forEach(function(key2, index) {
					if (index + 1 == keys.length) {
						that.$set(data, key2, val);
					} else {
						if (!data[key2]) {
							that.$set(data, key2, {});
						}
					}

					data = data[key2];
				});
			});
			callback && callback();
		}
	}
};
